@import '../../styles/_colors';
@import '../../styles/_fonts';

.articleright{
    display: grid;
    grid-template-areas: "photo content";
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 30px;
    max-width: 1140px;
    height: auto;
    display: grid;

    &__content{
        text-align: center;
        grid-area: content;
        a{
            color: rgb(93, 93, 93);
            text-decoration: none;
            transition: all 300ms ease-in;
        }  
        a:hover{
            color: $verde_oscuro;
        }

    }
    
    &__title{
        color: $verde_oscuro;
        font-size: 1.7rem;
    }
    &__url{
        cursor: pointer;
        font-size: 0.8rem;
    }
    
    &__text{
        padding: 2rem 0rem;
        mask-image: linear-gradient(to bottom, black 60%, transparent 100%);
        -webkit-mask-image: linear-gradient(to bottom, black 60%, transparent 100%);
    }
    &__img{
        display: flex;
        width: 100%;
        grid-area: photo;
    }

}

    @media (max-width: 1200px){

        .articleright{
            grid-template-areas: "photo"
            "content";
            grid-template-columns: 1fr;
            padding: 1rem;
            margin-top: 0rem;
        }
    }