


.Hero{
    display: flex;
    justify-content: center;
    align-items: flex-end;

    width: 100%;
    height: 150px;
    background-color: #602A9A;

    &__info img {
        max-width: 80vw;
    }

    &__arrow{
        display: none;
    }
}

/** For Desktop **/
@media (min-width: 767px) {
    .Hero{
        align-items: center;

        height: 600px;

        &__info img{
            width: 50vw;
        }

        &__arrow{
            display: block;
            position: absolute;
            margin: 0 auto;
            top: 500px;
            
            img{
                opacity: 0.3;
                width: 3rem;
                transition: all 300ms ease-in-out;
            }
            img:hover{
                cursor: pointer;
                opacity: 1;
            }
        }
        

    }
}

// Curva Superior

.custom-shape-divider-top-1626682207 {
    position: absolute;
    z-index: -99;
    top: 600px;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-top-1626682207 svg {
    position: relative;
    display: block;
    width: calc(169% + 1.3px);
    height: 55px;
}

.custom-shape-divider-top-1626682207 .shape-fill {
    fill: #602A9A;
}

/** For mobile devices **/
@media (max-width: 767px) {
    .custom-shape-divider-top-1626682207 {
        top: 150px;
    }

    .custom-shape-divider-top-1626682207 svg {
        width: calc(129% + 1.3px);
        height: 37px;
    }
}


