@import '../../styles/_colors';
@import '../../styles/_fonts';

.nav{
    width: 100%;
    height: auto;
    position: absolute;
    top: 0;
    transition: all 600ms cubic-bezier(0.39, 0.58, 0.57, 1);
    background: $lila;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &__r{
      display: none;
    }
    &__logo{
        display: flex;
        flex-direction: row;
        align-items: center;

        .burger{
            display: none;
            z-index: 99;
        }

        #logo img{
          width: 5rem;
          padding: 2rem;
        }
    } 
    &__menu{
        display: flex;
        flex-direction: row;

        list-style: none;
        a{
            text-decoration: none;
            color: white;
            padding: 0 2rem;
        } 
        .menu-item {
          a{
            transition: all 300ms;
          }
          a:hover{
            color: $verde_logo;
          }
        }
    }
    &__social{
    display: block;
    a img{
        width: 2.5rem;
        padding: 0 1rem;
        padding-right: 2rem;
    }

    }

    // Escalar boton de navegacion
    button {
      transform: scale(0.5);
    }
    
}

@media (max-width: 1100px){
    .nav{
        background: transparent;

        &__logo{
            #logo{
              display: none;
            }
            .burger{
              display: block;
            }
        } 

        &__r{
          img{
            width: 5rem;
            padding: 0rem;
          }
        }

        &__menu{
            transform: translateX(-100%);
            transition: all 500ms ease-out;
        }

        &__menu.show{
          transform: translateX(0);
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;

          position: relative;
          width: 100vw;
          height: 100vh;
          background: $lila;
          a{
            font-size: 1.5rem;
            display: block;
            padding: 1.5rem;
          } 
        }

        &__menu.hide{
          transform: translateX(-100%);
        }
        
        &__social{
            display: none;
        }
    }
}


