@import '../../styles/_colors';
@import '../../styles/_fonts';

.Bio{

    display: grid;
    grid-template-areas:
     "foto title"
     "foto text"
     "foto button"
     "logos logos"
    ;
    grid-template-columns: 20% 80%;
    margin: 0 auto;
    margin-top: 65px;
    max-width: 1140px;
    height: auto;

    &__image{
        margin: 0 auto;
        grid-area: foto;
    }

    &__title{
        display: flex;
        align-items: flex-end;
        justify-content: center;
        grid-area: title;

        color: $verde_oscuro;
        padding-bottom: 3rem;
    }

    &__text{
        grid-area: text;
        text-align: center;
        padding: 0 13rem;
        font-size: 1.2rem;
        text-justify: distribute;
        padding-bottom: 2rem;
    }

    &__button{
        display: flex;
        justify-content:center;
        align-items:center;

        text-decoration: none;
        border-style: none;
        margin: 0 auto;
        grid-area: button;
        font-size: 1rem;
        font-weight: 600;
        width: fit-content;
        height: 3rem;
        padding: 0 2rem;
        color: white;
        background-color: $verde_claro;
        border-radius: 5rem;

        transition: all 300ms ease-in-out;
    }

    &__button:hover{
        cursor: pointer;
        background: $verde_oscuro;
    }
    
    &__logos{
        margin-top: 3rem;
        display: flex;
        justify-content: center;
        grid-area: logos;
        img{
            height: 2.2rem;
            padding: 2rem 1rem;
        }
    }

}

@media (max-width: 768px){
    .Bio{
        display: grid;
        grid-template-areas:
        "foto"
        "title"
        "text"
        "button"
        "logos";
        grid-template-columns: 100%;
        &__image{
            max-width: 100%;
        }
        &__title{
            margin-top: 2rem;
            padding: 1rem;
            font-size: 2rem;
        }
        &__text{
            padding: 1rem;
        }
        &__button{
            margin-top: 1rem;
        }
        &__logos{
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));

            margin-top: 2rem;
            img{
                padding: 0.9rem;
                margin: 0 auto;
            }
        }
    }

}